}

/*! common core */
.e-control,
.e-css {
  font-family: "Roboto", "Segoe UI", "GeezaPro", "DejaVu Serif", "sans-serif", "-apple-system", "BlinkMacSystemFont";
  font-size: 12px;
  font-weight: normal;
}

.sf-license-warning {
  background: #e82824;
  color: #fff;
  font-family: 'Segoe UI';
  font-size: medium;
  left: 0;
  line-height: 45px;
  opacity: .75;
  position: fixed;
  right: 0;
  text-align: center;
  top: 0;
  z-index: 999999999;
}

.sf-license-close-btn {
  background: url("data:image/svg+xml;base64,");
  cursor: pointer;
  height: 20px;
  margin-top: 14px;
  padding-top: 10px;
  position: absolute;
  right: 20px;
  width: 20px;
}

.sf-license-warning a,
.sf-license-warning a:hover {
  color: #ffd800;
}

.e-error {
  color: #f44336;
  font-family: "Roboto", "Segoe UI", "GeezaPro", "DejaVu Serif", "sans-serif", "-apple-system", "BlinkMacSystemFont";
  font-size: 12px;
  font-weight: normal;
}

.e-control,
.e-control [class^='e-'],
.e-control [class*=' e-'] {
  box-sizing: border-box;
}

.e-control:focus,
.e-control *:focus {
  outline: none;
}

.e-rtl {
  direction: rtl;
  text-align: right;
}

.e-overlay {
  background-color: #383838;
  filter: alpha(opacity=50);
  height: 100%;
  opacity: .5;
  pointer-events: none;
  -ms-touch-action: none;
      touch-action: none;
  width: 100%;
}

.e-hidden {
  display: none;
}

.e-blazor-hidden {
  visibility: hidden;
}

.e-disabled {
  background-image: none;
  cursor: default;
  filter: alpha(Opacity=35);
  opacity: .35;
}

.e-ul {
  list-style-type: none;
}

.e-prevent-select {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.e-warning {
  color: #ffca1c;
}

.e-success {
  color: #22b24b;
}

.e-information {
  color: #489bd5;
}

.e-block-touch {
  -ms-touch-action: pinch-zoom;
      touch-action: pinch-zoom;
}

/*! animation keyframes */
@keyframes SlideLeftOut {
  from {
    transform: translate(0, 0);
  }
  to {
    transform: translate(-100%, 0);
  }
}

@keyframes SlideLeftIn {
  from {
    transform: translate(-100%, 0);
  }
  to {
    transform: translate(0, 0);
  }
}

@keyframes SlideRightIn {
  from {
    transform: translate(100%, 0);
  }
  to {
    transform: translate(0, 0);
  }
}

@keyframes SlideRightOut {
  from {
    transform: translate(0, 0);
  }
  to {
    transform: translate(100%, 0);
  }
}

@keyframes SlideBottomIn {
  from {
    transform: translate(0, 100%);
  }
  to {
    transform: translate(0, 0);
  }
}

@keyframes SlideBottomOut {
  from {
    transform: translate(0, 0);
  }
  to {
    transform: translate(0, 100%);
  }
}

@keyframes SlideTopIn {
  from {
    transform: translate(0, -100%);
  }
  to {
    transform: translate(0, 0);
  }
}

@keyframes SlideTopOut {
  from {
    transform: translate(0, 0);
  }
  to {
    transform: translate(0, -100%);
  }
}

@keyframes SlideRight {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes SlideLeft {
  from {
    width: 100%;
  }
  to {
    width: 0;
  }
}

@keyframes SlideDown {
  from {
    height: 0;
  }
  to {
    height: 100%;
  }
}

@keyframes SlideUp {
  from {
    height: 100%;
  }
  to {
    height: 0;
  }
}

@keyframes FadeIn {
  0% {
    filter: alpha(opacity=0);
    opacity: 0;
  }
  100% {
    filter: alpha(opacity=100);
    opacity: 1;
  }
}

@keyframes FadeOut {
  from {
    filter: alpha(opacity=100);
    opacity: 1;
  }
  to {
    filter: alpha(opacity=0);
    opacity: 0;
  }
}

@keyframes ZoomIn {
  from {
    transform: translate(0, 0) scale(0);
  }
  to {
    transform: translate(0, 0) scale(1);
  }
}

@keyframes ZoomOut {
  from {
    transform: translate(0, 0) scale(1);
  }
  to {
    transform: translate(0, 0) scale(0);
  }
}

@keyframes FadeZoomIn {
  from {
    filter: alpha(opacity=0);
    opacity: 0;
    transform: scale(0);
  }
  to {
    filter: alpha(opacity=100);
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes FadeZoomOut {
  from {
    filter: alpha(opacity=100);
    opacity: 1;
    transform: scale(1);
  }
  to {
    filter: alpha(opacity=0);
    opacity: 0;
    transform: scale(0);
  }
}

@keyframes FlipRightDownIn {
  from {
    transform: perspective(400px) rotateY(-180deg);
    transform-origin: right center;
    transform-style: preserve-3d;
  }
  to {
    transform: rotateY(0deg);
    transform-origin: right center;
    transform-style: preserve-3d;
  }
}

